import axiosInstance from '../api/axios'; 
import { UnicornCompanies } from '../models/unicorn_companies';

export async function getPageData(
    pageno: number = 1
  ) : Promise<{
    companyList: UnicornCompanies[];
    pageNo: number | null;
    newOffset: number | null;
    totalItems: number;
  }>  {  
  
    let response, unicorn_companies;
    try {  
      response = await axiosInstance.get("/api/unicorncompanies/pages", { params: { 'page[pageno]': pageno } });
    //   console.log("RESPONSE >> ", response?.data);
      unicorn_companies = parseAllObjects<UnicornCompanies>(response.data.data.data);
    } catch (error) {  
      // Handle the error here  
      console.error(error);  
    }  
  
    return {
      companyList: unicorn_companies,
      pageNo: response?.data.data.pageInfo.page,
      newOffset: response?.data.data.pageInfo.pageSize,
      totalItems: response?.data.data.pageInfo.totalRowCount
    };
  };

  export async function  getAllData(){  
    try {  
      const response = await axiosInstance.get("/api/unicorncompanies");
      let unicorn_companies = parseAllObjects<UnicornCompanies>(response.data.data);
      return unicorn_companies;
    } catch (error) {  
      // Handle the error here  
      console.error(error);  
    }  
  };
  
  function parseAllObjects<T>(json: any): T[] | null {
    try {
      const jsonString = JSON.stringify(json);
      return JSON.parse(jsonString) as T[];
    } catch (error) {
      console.error("Error parsing JSON:", error);
      return null;
    }
  }
  